.d-flex-row{
    display: flex;
}

.d-flex-column{
    display: flex;
    flex-direction: column;
}

.justify-content-center{
    justify-content: center;
}

.align-items{
    align-items: center;
}

.justify-content-around{
    justify-content: space-around;
}

/*************************************************************************************************************************/
.graph {height: 12px; background-color: #222222;border: 1px solid gray;margin-bottom: 12px;}

.html,.css {display: block;height: 12px;width: 90%; line-height: 40px;
    text-align: right; box-sizing: border-box; color: #fff;
    background-color: #FF724C; animation: stack1 2s 1;}
.sql, .database {display: block;height: 12px;width: 85%; line-height: 40px;
    text-align: right; box-sizing: border-box; color: #fff;
    background-color: #FF724C; animation: stack2 2s 1;}
.c, .php {display: block;height: 12px;width: 60%; line-height: 40px;
    text-align: right; box-sizing: border-box; color: #fff;
    background-color: #FF724C; animation: stack3 2s 1;}
.python,.react {display: block;height: 12px;width: 75%; line-height: 40px;
    text-align: right; box-sizing: border-box; color: #fff;
    background-color: #FF724C; animation: stack4 2s 1;}
.java {display: block;height: 12px;width: 70%; line-height: 40px;
    text-align: right; box-sizing: border-box; color: #fff;
    background-color: #FF724C; animation: stack5 2s 1;}

@keyframes stack1 {
    0% {width: 0;}
    100% {width: 90%;}
}
@keyframes stack2 {
    0% {width: 0;}
    100% {width: 85%;}
}
@keyframes stack3 {
    0% {width: 0;}
    100% {width: 60%;}
}
@keyframes stack4 {
    0% {width: 0;}
    100% {width: 75%;}
}
@keyframes stack5 {
    0% {width: 0;}
    100% {width: 70%;}
}






