.page {
    height: 100vh;
    display: flex;
    background: url("/public/icons/background.jpg") fixed no-repeat;
    background-size: cover;
    font-family: "맑은 고딕 Semilight";
}

.profile {
    height: 180px;
    border-bottom: 1px solid gray;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    color: white;
    font-size: 1.5em;
}

.profile img {
    width: 80px;
    padding-top: 20px;
    border-radius: 50%;
}

.nav-btns {
    background-color: #222222;
    height: 100%;
    width: 130px;
}

.link{
    height: 100px;
    border-bottom: 1px solid gray;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.link:hover{
    border-right: 3px solid #ff724c;
}
.link:active{
    border-right: 3px solid #ff724C;
}
  .link li {
